import { useEffect } from 'react';
import { LoadingIcon } from 'components/Loading';
import { FullSizeFlexBox } from 'components/styled';
import { useSearchParams } from 'react-router-dom';

const RedirectDeposit = () => {
  const [searchParams] = useSearchParams();
  const success = searchParams.get('success')

  useEffect(() => {
    window.parent.postMessage({ isDeposit: true, isSuccess: success }, '*')
  }, [])

  return (
    <FullSizeFlexBox display="flex" alignItems="center" justifyContent="center">
      <LoadingIcon size={60} />
    </FullSizeFlexBox>
  )
}

export default RedirectDeposit
